
import { Vue, Options  } from 'vue-class-component';
import CountDown from '@/components/CountDown.vue'; 
@Options({
  components: {
    CountDown
  },
  props: {
    isOpen: Boolean
  },
  emits: ['gameStarted']
})
export default class GameStarter extends Vue {
	isOpen=false;
	state='instruction';

	gameStarted(){
		this.state='started'
		this.$emit('gameStarted');
	}
}
