
import { Vue,Options } from 'vue-class-component';

@Options({
  props: {
	start: Boolean,
	timer: Number
  },
  watch: {
    start() {
		if(this.start){
		this.count = this.timer;
		}
    }
  },
  emits: ['isOver']
})
export default class CountDown extends Vue {
	start=false;
	timer=10;
	count=0;

	nextCount(){
		this.count = (this.count>0?this.count-1:0)
		if(this.count<=0){
			this.$emit('isOver')
		}
	}
}
